.mainContainer{
    position: relative;
    max-height: 100vh;
    max-width: 1300px;
    display: flex;
    justify-content: center;
    align-items: center;
    left: 12%;
    margin-top: 45px;
    /*border: 2px solid aqua;*/

}

.container_log{
    /*position: absolute;*/
    width: 1200px;
    min-height: 560px;
    max-height: 565px;
    left: 600px;
    top: 80px;

    background: rgba(249, 249, 254, 0.6);
    /*border: 1px solid rgba(42, 2, 2, 0.9);*/
    border-radius: 8px;
    -webkit-box-shadow: -4px 5px 4px 1px rgba(157, 151, 151, 0.5);
