    box-shadow: -4px 5px 4px 1px rgba(157, 151, 151, 0.5);
}
.title{
    width: 100%;
    height: 71px;
    display: flex;
    justify-content: center;
    align-items: center;
    left: 559px;
    top: 85px;

    /*font-family: 'Poppins';*/
    font-style: normal;
    font-weight: 411;
    font-size: 20px;
    line-height: 39px;
    /* identical to box height */
    color: #2D2E46;

}
h2{
    text-align: center;
}
h3{
    color: #bbbefa;
    font-size: 17px;
}


.blockAvatar{
    /*border: 1px solid #ff9900;*/
    border-radius: 2px;
    width: 230px;
    padding: 5px;
    background: rgba(28, 30, 37, 0.49);
    box-shadow: 0 3px 10px rgb(143, 33, 49), inset 0 1px 0 rgba(255, 255, 255, 0.42);
}
b{
    color: #c4fabb;
}

.eyes{

    width: 100%; /* Ширина поля */
    /*padding: 7px; !* Поля *!*/
    box-sizing: border-box; /* Алгоритм расчёта ширины */
    box-shadow: inset 0 1px 5px rgba(0,0,0,0.2); /* Тень внутри */
    border-bottom-color: rgba(255, 255, 255, 0.87);/* Параметры рамки */
    color: #251e1e; /* Цвет текста */
    background: url(/src/n2-features/f1-auth/a1-login/eye.png) no-repeat 345px 50%;/* Картинка пользователя */
    padding-right: 30px;

}


.btnContainer{
    height: 33px;
    width: 232px;
    display: flex;
    justify-content: center;
    align-items: center;
    left: 0;
    top: 0;
    /*border: 1px #5cdecb;*/

}
.btn{
    height: 29px;
    width: 90px;
    background: rgba(190, 47, 65, 0.92);
    box-shadow: 0 3px 10px rgba(12, 5, 9, 0.84), inset 0 1px 0 rgba(255, 255, 255, 0.42);
    border-radius: 30px;
    color: white;
    margin:8px 12px 10px;
}
.btn:hover{
    height: 32px;
    width: 97px;
    transform: scale(1);
    transition: all 1s ease;
    margin-left:10px;
}
.btnDise{
    height: 29px;
    width: 90px;
    font-size: 16px;
    background: rgba(232, 226, 226, 0.57);
    box-shadow: 0 3px 10px rgb(143, 33, 49), inset 0 1px 0 rgba(255, 255, 255, 0.42);
    border-radius: 30px;
    color: #130d0d;
    margin:16px 15px 16px;
}
.btnDise:hover{
    height: 35px;
    width: 127px;
    transform: scale(1);
    transition: all 1s ease;
    margin-right:10px;
}


.hContainer, .aContainer{
    height: 55px;
    width: 375px;
    display: flex;
    justify-content: center;
    align-items: center;
    left: 0;
    top: 0;
}


.imgAvatar{
    width: 80px;
    height: 80px;
    margin: 10px 25px 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    background-size: cover;
    background-position: center;
    position: relative;
    border-radius: 50%;
    border: 1px solid white;

}
